import GameView from "./InfiniteHeights.GameView";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BackGround extends cc.Component {

   isAdd = false;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    update(dt) {
        if(this.node.y <= -1600) {
            this.node.destroy();
            return;
        }

        if(!this.isAdd) {
            if(this.node.y === 640) {
                console.log("sadasd")
                this.isAdd = true;
                // GameView.instance.genBackGround();
              
            }
        }

        this.node.y -= 2;
    }
}
